  border-bottom: 1px solid var(--c-border);
}
.tabs-nav ul li {
  display: inline-block;
  border-width: 0 0 2px 0;
  border-style: solid;
  border-color: transparent;
  margin-right: 5px;
}
.tabs-nav ul li a {
  display: block;
  padding: 10px 15px;
  color: var(--c-text-light);
  font-weight: 700;
}
.tabs-nav ul li.active {
  border-color: var(--c-text-main);
}
.tabs-nav ul li.active a {
  color: var(--c-text-main);
}

/* Tab content */
.tabs-content {
  padding: 30px 0 0;
  overflow: hidden;
}

.project-block .card {
  margin: 0px 0px 1.5em;
  position: relative;
  cursor: pointer;
  object-fit: cover;
  background-color: var(--c-section-bg-light);
  background-position: center center;
  background-repeat: no-repeat;
  overflow: hidden;
  padding: 1.4rem 2rem;
  display: flex;
  border-radius: 9px;
  justify-content: flex-end;
  min-height: 330px;
}
.project-block .card:before {
  content: "";
  pointer-events: none;
  display: block;
  position: absolute;
  inset: 0px;
  z-index: -1;
  background: radial-gradient(circle at center center, white 10%, whitesmoke 11%, whitesmoke 100%);
}
.project-block .card img {
  position: absolute;
  object-fit: cover;
  width: 100% !important;
  height: 100% !important;
  left: 0px !important;
  top: 0px !important;
}
.project-block .card .card-content {
  opacity: 0;
  transform: translateY(10%);
  transition: opacity 300ms ease-in-out 0s, transform 300ms ease-in-out 0s;
  color: #fff;
}
.project-block .card .card-content h3, .project-block .card .card-content p {
  color: #fff;
}
.project-block .card:after {
  content: "";
  pointer-events: none;
  position: absolute;
  z-index: 1;
  inset: 0px;
  opacity: 0.3;
  background: linear-gradient(rgba(0, 0, 0, 0.1) 10%, rgba(0, 0, 0, 0.78) 80%);
}
.project-block .card:hover .card-content, .project-block .card:focus .card-content {
  z-index: 9;
  transform: none;
  opacity: 1;
}
.project-block .card:hover:after, .project-block .card:focus:after {
  opacity: 1;
}

.badge {
  background: var(--c-text-light);
  padding: 4px 10px;
  border-radius: 50px;
  text-transform: capitalize;
  font-size: 11px;
  margin-right: 6px;
  color: var(--c-outline-text);
  font-weight: 500;
}
.badge.badge-lg {
  padding: 10px;
  font-size: 13px;
  border-radius: 5px;
}

.project-block-information {
  display: none;
}
.project-block-information .overlay {
  z-index: 1000;
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0px;
  right: 0px;
  background: rgba(0, 0, 0, 0.8);
}
.project-block-information aside {
  animation-name: fadeLeft;
  animation-duration: 0.5s;
  animation-fill-mode: both;
  background: var(--c-body-bg);
  width: 100%;
  height: 100%;
  position: fixed;
  right: 0px;
  top: 0px;
  z-index: 1001;
  transition: all 0.3s linear 0s;
  will-change: opacity, transform;
}
@media (min-width: 768px) {
  .project-block-information aside {
    width: 300px;
  }
}
@media (min-width: 1200px) {
  .project-block-information aside {
    width: 450px;
  }
}
.project-block-information aside .aside-inner {
  position: relative;
  padding: 20px 20px 60px;
  overflow-x: overlay;
  max-height: 100%;
}
.project-block-information aside .aside-inner .owl-carousel .owl-item img {
  height: 230px;
  object-fit: contain;
  background: var(--c-section-bg-light);
  border-radius: 10px;
}
.project-block-information aside .aside-inner h4 {
  margin-top: 40px;
  margin-bottom: 10px;
}
.project-block-information aside .aside-inner p {
  font-size: 14px;
  line-height: 1.3;
  letter-spacing: 0;
  font-family: "Mulish";
}
.project-block-information aside .aside-inner .popup-gallery {
  margin-bottom: 20px;
}
.project-block-information aside .aside-inner .popup-gallery a {
  display: block;
}
.project-block-information aside .aside-inner .popup-gallery a:hover {
  cursor: -webkit-zoom-in;
}
.project-block-information aside .aside-inner .aside-fixed-btn {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 12px;
  font-size: 14px;
  width: 100%;
  border-radius: 0;
  text-align: center;
}
.project-block-information aside .aside-inner .aside-fixed-btn svg {
  margin-left: 10px;
  fill: white;
  height: 16px;
  width: 16px;
}
.project-block-information aside .project-block-header {
  margin-bottom: 1rem;
  padding-bottom: 0.8rem;
  border-bottom: 1px solid var(--c-border-light);
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.project-block-information aside .project-block-header a {
  font-size: 14px;
}

@keyframes fadeLeft {
  0% {
    opacity: 0.8;
    transform: translateX(80%);
  }
  100% {
    opacity: 1;
    transform: translateX(0%);
  }
}
.owl-carousel .owl-next, .owl-carousel .owl-prev {
  position: absolute;
  top: 50%;
  font-size: 40px;
  padding: 10px !important;
  background: #ddd;
  height: 40px;
  line-height: 40px;
  width: 40px;
  display: flex;
  align-items: center;
  transform: translateY(-50%);
}
.owl-carousel .owl-prev {
  left: 0;
}
.owl-carousel .owl-next {
  right: 0;
}

.text-fill {
  fill: var(--c-text-main);
}

.primary-fill {
  fill: var(--c-primary);
}

.secondary-fill {
  fill: #1AD1A1;
}

.primary-stroke {
  stroke: var(--c-primary);
}